#welcome {
    min-height: 100vh;
    background: url("../../assets/images/background4.jpg") center center fixed;
    background-size: cover;
    display: flex;
    align-items: center;
}
  
#welcomeContainer {
    background-color: rgba(255, 255, 255, 0.75);
}

#headshotContainer {
    display: flex;
    align-items: center;
}

#headshot {
    max-width: 100%;
} 

@keyframes rToL {
    0%   {left:900px; top:0px;}
    100% {left:0px; top:0px;}
}

#welcomeH1 {
    position: relative;
    font-size: 6rem;
    animation-name: rToL;
    animation-duration: 3s;
}

.welcomeH2 {
    position: relative;
    font-size: 3rem;
    animation-name: rToL;
    animation-duration: 4s;
}

#aboutBtn {
    position: relative;
    animation-name: rToL;
    animation-duration: 5s;
}

@media screen and (max-width: 1000px) {
    
    #welcomeH1 {
        font-size: 5rem;
    }
    .welcomeH2 {
        font-size: 2rem;
    }

}

@media screen and (max-width: 768px) {
    
    #welcomeH1 {
        font-size: 4rem;
    }
    .welcomeH2 {
        font-size: 1.5rem;
    }

}

@media screen and (max-width: 576px) {
    
    #welcomeH1 {
        font-size: 3rem;
    }
    .welcomeH2 {
        font-size: 1rem;
    }
    #welcome {
        padding: 15px;
    }

}


